@import "../../App/mixin.scss";

.wrapper{
    @include wrapper;
}
.header{
    position: sticky;
    top: 0;
    z-index: 1000;
    background-color: #2231AA;
    &Container{
        height: 84px;
        display: flex;
        flex-direction: row;
        align-items: center;
    }
    &Burger{
        position: relative;
        height: 18px;
        width: 30px;
        margin:0 30px;
        cursor: pointer;
        &::before{
            content: "";
            position: absolute;
            top: 0;
            width: 25px;
            background-color: white;
            height: 2px;
        }
        &::after{
            content: "";
            position: absolute;
            bottom: 0;
            width: 25px;
            background-color: white;
            height: 2px;
        }
        & span{
            position: absolute;
            top: 8px;
            width: 25px;
            background-color: white;
            height: 2px;
        }
    }
    &Search{
        height: 100%;
        width: 90%;
        display: flex;
        flex-direction: row;
        align-items: center;
        & input{
            padding: 0 30px;
            height: 100%;
            width: 100%;
            outline: none;
            color: whitesmoke;
            background-color :#2231AA;
            border: none;
            border-left: 2px solid #5360CD;
            border-right: 2px solid #5360CD;
            transition: 0.5s;
            &:hover{
                background-color :#5360CD;
                transition: 0.5s;
            }
            &::placeholder{
                font-family: 'Inter';
                font-style: normal;
                font-weight: 400;
                font-size: 16px;
                line-height: 24px;
                color: rgba(255, 255, 255, 0.5);
            }
        }
        & button{
            height: 100%;
            width: 86px;
            padding: 30px 36px 36px 30px;
            background-color :#2231AA;;
            border: none;
            border-right: 2px solid #5360CD;
            cursor: pointer;
        }
    }
    &User{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        padding: 17.5px 0;
        width: 236px;
        & p{
            font-family: 'Inter';
            font-style: normal;
            font-weight: 600;
            font-size: 16px;
            line-height: 34px;
            color: #FFFFFF;
        }
    }
}








